@font-face {
    font-family: 'QuattrocentoSans-Regular';
    src: local('QuattrocentoSans-Regular'), url(../../fonts/QuattrocentoSans-Regular.ttf) format("truetype");
}

img {
 object-fit: contain;
}

img:hover {
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

@media only screen and (max-width: 1200px){
    img {
        width: 409px;
        object-fit: contain;

    }
}

@media only screen and (max-width: 600px){
    img {
        width: 190px;
        object-fit: contain;

    }
}

.info {
    font-family: 'QuattrocentoSans-Regular';
    margin: 2px;
}